import React, { Component, PropTypes } from 'react';
import styled from 'styled-components';
import { SketchPicker } from 'react-color';
import {
  Heading,
  LineSeparator,
  Layout,
  Box,
  Icon,
  Padder,
  Text,
} from 'react-components-kit';

class ColorBox extends Component {
  shouldComponentUpdate(nextProps) {
    if ((nextProps.color !== this.props.color) ||
        (nextProps.selected !== this.props.selected)
    ) {
      return true;
    }
    return false;
  }

  render() {
    const { color, selected } = this.props;
    return (
      <ColorBoxWrapper
        color={color}
        onClick={this.props.onClick}
        selected={selected}
      />
    );
  }
}


class ThemeChanger extends Component {
  constructor(props) {
    super(props);
    this.selectColorBox = this.selectColorBox.bind(this);
    this.exportTheme = this.exportTheme.bind(this);
    this.state = {
      selected: null,
    }
  }

  selectColorBox(colorName) {
    this.setState({ selected: colorName });
  }

  exportTheme() {
    const { theme } = this.props;
    const data = {};

    Object.entries(theme)
      .filter(([colorName]) => colorName.indexOf('Base') === -1)
      .map(([colorName, colorValue]) => { data[colorName] = colorValue; });

    const json = JSON.stringify(data, null, 2);

    const tab = window.open(
      `data:text/json,${encodeURIComponent(json)}`, '_blank'
    );

    // Switch to new tab
    tab.focus();
  }

  render() {
    const { theme } = this.props;

    // Filter base colors since they are `Color` objects not color values
    const colors = Object.entries(theme)
      .filter(([colorName]) => colorName.indexOf('Base') === -1)
      .sort((a, b) => {
        const first = a[0].toLowerCase();
        const second = b[0].toLowerCase();
        return (first < second) ? -1 : (first > second) ? 1 : 0;
      })

    return (
      <ThemeChangerWrapper isOpen={this.props.isOpen}>
        <ThemeChangerPanel isOpen={this.props.isOpen}>
          <Layout align='center'>
            <Box flex='1'>
              <Heading el='h2'>
                {this.state.selected || 'Choose color to change'}
              </Heading>
            </Box>
            <Padder />
            <Box>
              <Layout column align='center'>
                <Box>
                  <ExportThemeBtn onClick={this.exportTheme}>
                    <Icon className='ion-ios-download' size='24px' color='#fff' />
                  </ExportThemeBtn>
                </Box>
                <Padder vert='8px' />
                <Box>
                  <Text size='12px' color='#666'>
                    Download theme (JSON)
                  </Text>
                </Box>
              </Layout>
            </Box>
          </Layout>

          <LineSeparator horizontal />
          <Colors>
            {colors.map(([colorName, colorValue]) =>
              <ColorBox
                color={colorValue}
                label={colorName}
                onClick={() => this.selectColorBox(colorName)}
                selected={this.state.selected === colorName}
                key={colorName}
              />
            )}
          </Colors>
          <ColorPicker>
            {this.state.selected &&
              <SketchPicker
                color={
                  this.props.theme[this.state.selected] || '#fff'
                }
                onChangeComplete={(color, event) =>
                  this.props.updateThemeColor(
                    this.state.selected,
                    color.hex
                  )
                }
              />
            }
          </ColorPicker>
        </ThemeChangerPanel>

        <Backdrop onClick={this.props.close} isOpen={this.props.isOpen} />
      </ThemeChangerWrapper>
    );
  }
}

const ThemeChangerWrapper = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 999999;
  ${props => !props.isOpen && 'pointer-events: none;'}
`;
const ThemeChangerPanel = styled.div`
  width: 400px;
  height: 100vh;
  position: fixed;
  right: 0;
  top: 0;
  background-color: #fff;
  padding: 16px;
  transition: transform 0.3s ease-in;
  will-change: transform;
  transform: translateX(${props => props.isOpen ? '0px' : '400px'});
  ${props => props.isOpen && 'box-shadow: 0px 0px 12px rgba(0,0,0,0.5);'}
  z-index: 999998;
`;
const Backdrop = styled.div`
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 999997;
  background-color: rgba(0,0,0,0.2);
  opacity: 0;
  transition-delay: 100;
  transition: opacity 0.2s ease;
  ${props => props.isOpen && 'opacity: 1;'}
`;
const Colors = styled.div`
  display: flex;
  flex-wrap: wrap;
`;
const ColorBoxWrapper = styled.div`
  width: 40px;
  height: 40px;
  margin-bottom: 8px;
  margin-right: 8px;
  border: 3px solid white;
  ${props => props.selected && 'border-color: slategrey;'}
  background-color: ${props => props.color};
`;
const ColorPicker = styled.div`
  width: 100%;
  margin-top: 32px;
`;
const ExportThemeBtn = styled.button`
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: rebeccapurple;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
`;

ThemeChanger.propTypes = {
  updateThemeColor: PropTypes.func.isRequired,
  close: PropTypes.func.isRequired,
  theme: PropTypes.object.isRequired,
};

export default ThemeChanger;



